  input {
    height: 4rem;
    width: 100%;
    background: transparent;
    border: 1px solid #ebebeb;
    border-radius: 6px;
    padding: 0 2rem;
    resize: none;
    overflow: auto;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 1.7rem;
    letter-spacing: 0.05em;
    color: #ffffff;

