    &::placeholder {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 1.7rem;
      letter-spacing: 0.05em;
      color: #ffffff;
    }

    &:focus {
      outline: none;
    }
  }

  button {
    border: none;
    width: 3.1rem;
    height: 2.6rem;

    background-image: url('../../img/chatInputSent.svg');
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: 100%;

    cursor: pointer;

    &:hover {
      background-color: #afafaf;
    }
  }
