.search {
        position: relative;

        input[type="search"] {
            width: 32rem;
            height: 3.5rem;
            background: #a2a7bb;
            border-radius: 20px;

            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 500;
            font-size: 1.3rem;
            line-height: 1.6rem;
            letter-spacing: 0.05em;

            padding: 0 1.5rem 0 5.2rem;
            
            color: #FFFFFF;

            
            outline: none;
            border: 0.1rem solid #c0c5d8;
            text-decoration: none;
            box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);

            // Close
            &::-webkit-search-cancel-button {
                -webkit-appearance: none;
                
                width: 2rem;
                height: 2rem;
                background: url('../../img/searchCloseIcon.svg');
            }

            @media (max-width: 870px) {
                width: 28rem;
            }

            @media (max-width: 425px) {
                width: 100%;
            }
        }

        .dropBtn {
            position: absolute;
            top: 0.9rem;
            left: 1.7rem;

            input {
                display: inline-block;
                width: 2.4rem;
                height: 1.8rem;
                background: url('../../img/searchBarsIcon.svg');
                background-repeat: no-repeat;
                cursor: pointer;

                border: none;

                &:focus {
                    outline: none;
                    border: none;
                }
                
            }
        }

        .dropDown {
            z-index: 100;
            
            position: absolute;
            top: 3.39rem;
            left: 1.3rem;

            width: calc(100% - 2.5rem);

            overflow: hidden;
            
            border: 0.1rem solid #c0c5d8;

            // background: #a2a7bb;

            background: rgba(162, 167, 187, 0.3);
            backdrop-filter: blur(10px);
            .dropDownHeader {
                width: 100%;
                height: 2.5rem;

                color: #ffffff;
                font-size: 1.2rem;
                font-weight: 600;
                
                display: flex;
                justify-content: center;
                align-items: center;

                // background: #9396a5;

                background: rgba(147, 150, 165, 0.1);
                backdrop-filter: blur(5px);
            }

                .groupRadio {
                    padding: 0.7rem 0.7rem;
                    border-top: 0.1rem solid #c0c5d8;

                    &:last-child {
                        border-bottom: none;
                    }

                    input {
                        position: absolute;
                        z-index: -1;
                        opacity: 0;
                    }

                    label {
                        color: #ffffff;
                        font-weight: 500;
                        font-size: 1.35rem;
                    }
                    input + label {
                        display: inline-flex;
                        align-items: center;
                        user-select: none;
                    }
                    input + label::before {
                        content: '';
                        display: inline-block;
                        width: 1.5em;
                        height: 1.5em;
                        flex-shrink: 0;
                        flex-grow: 0;
                        border: 1px solid #adb5bd;
                        border-radius: 0.25em;
                        margin-right: 0.5em;
                        background-repeat: no-repeat;
                        background-position: center center;
                        background-size: 50% 50%;
                    }

                    input:checked+label::before {
                        border-color: #0b76ef;
                        background-color: #0b76ef;
                        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
                    }

                    /* стили при наведении курсора на checkbox */
                    input:not(:disabled):not(:checked)+label:hover::before {
                        border-color: #b3d7ff;
                    }
                    /* стили для активного состояния чекбокса (при нажатии на него) */
                    input:not(:disabled):active+label::before {
                        background-color: #b3d7ff;
                        border-color: #b3d7ff;
                    }
                    /* стили для чекбокса, находящегося в фокусе */
                    input:focus+label::before {
                        box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
                    }
                    /* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
                    input:focus:not(:checked)+label::before {
                        border-color: #80bdff;
                    }
                    /* стили для чекбокса, находящегося в состоянии disabled */
                    input:disabled+label::before {
                        background-color: #e9ecef;
                    }
                }
        }

    }