.chat {
    height: 100%;

    .chatContent {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        // overflow: hidden;

        position: relative;
        
        .chatMessages {
            height: calc(100% - 14rem);

            background: #C0C0C0;
            padding: 1rem 1.5rem;
            overflow: auto;

            &::-webkit-scrollbar {
                width: 7px;
                background: #444d73;
            }
        
            &::-webkit-scrollbar-thumb {
                background: #a2a7bb;
            }
        
            &::-webkit-scrollbar-track {
                background: #C0C0C0;
            }

            @media (max-width: 814px) {
                height: calc(100% - 14rem);
                // height: calc(calc(var(--vh, 1vh) * 100) - 14rem);
            }

            .chatMessagesWrapper {
            }
        }
        .chatInputWrapper {
            min-height: 8rem;
            background: #ABABAB;
            border-top: 2px solid #A0A0A0;

            // @media (max-width: 814px) {
            //     position: fixed;
            //     width: 100%;
            //     bottom: 0;
            //     left: 0;
            //     right: 0;
            // }

        }
    }
}